import { useEffect, useState, createRef } from 'react';
import { ARController } from 'artoolkit5-js';
import cameraData from '../static/camera_para.dat';
// import detectNFTMarker from '../utils/markerTracker';

const initCamera = async (video) => {
  const constraints = {
    audio: false,
    video: {
      //facingMode: "environment",
      facingMode: 'user',
      width: 640,
      height: 480,
      frameRate: { max: 30 },
    },
  };
  const stream = await navigator.mediaDevices.getUserMedia(constraints);

  console.log('init camera');

  video.current.srcObject = stream;

  // setVideoStream(stream);


  // video.onloadedmediadata = () => {
  //   console.log('video loadedmediadata');
  //   video.play();
  // };
};

const onError = (error) => {
  console.error('Error while intizalizing arController', error);
};

const onload = (arController) => {
  arController
    .loadNFTMarker('ar-test/static/markers/digitalls_marker')
    .then((markerId) => {
      arController.trackNFTMarkerId(markerId);
    })
    .catch((err) => {
      console.log('Error in loading marker on Worker', err);
    });

  arController.addEventListener('getNFTMarker', (ev) => {
    const markerResult = {
      type: 'found',
      matrix: JSON.stringify(ev.data.matrix),
    };
    console.log('markerFound', markerResult);
  });
};

const initAr = async () => {
  ARController.initWithDimensions(640, 480, cameraData)
    .then(onload)
    .catch(onError);

  // const digitallsMarkerId = await controller.artoolkit.addNFTMarker(
  //   controller.id,
  //   'ar-test/static/markers/digitalls_marker'
  // );
  // detectNFTMarker(controller, digitallsMarkerId);
};

const ViewerArToolkit = () => {
  // const [videoStream, setVideoStream] = useState(null);
  const video = createRef();
  // const handleLoadedMetadata = () => {
  //   console.log('loaded metadata');
  //   video.play();
  // };
  // const handleLoadedData = () => {
  //   console.log('Camera is ready');
  //   initAr();
  // };

  useEffect(() => {
    initCamera(video);
  }, [video]);

  return (
    <video
      ref={video}
      autoPlay
      // onLoadedMetadata={() => handleLoadedMetadata}
      // loadedData={() => handleLoadedData}
    ></video>
  );
};

export default ViewerArToolkit;
